@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
/* Themes */

.light {
    --bg-header-footer: #df744a;
    --bg-body: #f8f8ff;
    --text-head: #ffffff;
    --text-body: #333333;
    --text-body-invert: #fefefe;
    --bg-card: #60bbd4;
    --bg-form: #e5faa5;
    --form-btn: #60bbd4;
    --del-btn: #fa7c92;
    --del-btn-hover: #ff5349;
    --com-btn: #66ab8c;
    --com-btn-hover: #195905;
    --bg-howto: #fabbbb;
    --bg-list: #fff7c0;
    --bg-expired: #fa7c92;
    --bg-done: #66ab8c;
    --bg-full-name: #df744a;
    --full-name-body: #ffffff;
    --icon-expired: #fa7c92;
    --icon-done: #66ab8c;
}

.dark {
    --bg-header-footer: #6c7983;
    --bg-body: #2a2e35;
    --text-head: #ffffff;
    --text-body: #ffffff;
    --text-body-invert: #f5f5f5;
    --bg-card: #684da5;
    --bg-form: #383838;
    --form-btn: #8e873b;
    --del-btn: #cf6679;
    --del-btn-hover: #ff5349;
    --com-btn: #66ab8c;
    --com-btn-hover: #195905;
    --bg-howto: #805555;
    --bg-list: #12181b;
    --bg-expired: #cf6679;
    --bg-done: #183b1d;
    --bg-full-name: #2a2e35;
    --full-name-body: #ffffff;
    --icon-expired: #cf6679;
    --icon-done: #66ab8c;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    height: 100%;
}

body {
    font-family: 'Montserrat', sans-serif;
    line-height: 1.5;
    color: var(--text-body);
    background: var(--bg-body);
    height: 100%;
    transition: background 500ms ease-in-out, color 1000ms ease-in-out;
}

#root {
    height: 100%;
}

.hidden {
    display: none;
}

/* App */

.app-container {
    display: grid;
    align-content: space-between;
    min-height: 100vh;
    color: var(--text-body);
    background: var(--bg-body);
}

/* section global styles */
.section__heading {
    display: grid;
    grid-template-columns: 40px 1fr 40px;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
}

.section__icon-container {
    width: 40px;
    height: 40px;
    overflow: hidden;
}

.section__icon {
    font-size: 40px;
    line-height: 1;
    color: var(--text-body);
}

.section__title {
    font-size: 24px;
    font-weight: 700;
    color: var(--text-body);
}

.section__toggle {
    width: 40px;
    height: 40px;
    overflow: hidden;
    cursor: pointer;
}

.section__btn {
    font-size: 40px;
    line-height: 1;
    color: var(--text-body);
}

.section__btn:hover {
    color: rgba(0, 0, 0, 0.5);
}

.header {
	display: grid;
	grid-template-columns: 450px 1fr;
	height: 100px;
    overflow: hidden;
    margin-bottom: 20px;
    background: var(--bg-header-footer);
}

.header__logo-container {
	justify-self: start;
	width: 100%;
	height: 100%;
    overflow: hidden;
}
.header__logo-container svg {
	width: 450px;
}

.header__logo {
	width: 500px;
    fill: var(--text-head);
}

.header__nav-container {
	display: grid;
	justify-content: end;
	height: 100%;
	margin-right: 20px;
}

.header__nav-bar {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	align-items: center;
	grid-column-gap: 20px;
	-webkit-column-gap: 20px;
	        column-gap: 20px;
	width: auto;
	height: 100%;
	list-style-type: none;
}

.header__nav-item {
	display: block;
	width: auto;
	height: auto;
	overflow: hidden;
	cursor: pointer;
}

.header__icon-holder {
	width: 40px;
	height: 40px;
	overflow: hidden;
}

.header__icon {
	vertical-align: middle;
	font-size: 40px;
	line-height: 1;
	color: var(--text-head);
}

.header__nav-item:hover .header__icon {
	color: rgba(0, 0, 0, 0.5);
}

@media(max-width: 767px) {
	.header {
		grid-template-columns: 190px 1fr;
        height: 50px;
	}

	.header__logo-container svg {
		width: 230px;
	}

	.header__nav-container {
		margin-right: 10px;
	}
	
	.header__nav-bar {
		grid-column-gap: 10px;
		-webkit-column-gap: 10px;
		        column-gap: 10px;
	}

	.header__icon-holder {
		width: 30px;
		height: 30px;
	}

	.header__icon {
		font-size: 30px;
	}
}
.htu {
	display: block;
	margin: 15px 30px;
	background: var(--bg-howto);
	border-radius: 10px;
	padding: 10px;
}

.htu__container {
	padding: 20px 10px;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 1fr;
}

.htu__background {
	grid-row: 1 / 2;
	grid-column: 1 / 2;
	justify-self: end;
	align-self: center;
	opacity: .15;
	font-size: 350px;
}

.htu__content {
	grid-row: 1 / 2;
	grid-column: 1 / 2;
}

.htu__content * {
	margin: 0 0 10px 0;
}

@media(max-width: 767px) {
	.htu {
		margin: 0 0 15px;
		border-radius: 0;
		padding: 10px;
	}

	.htu__background {
		display: none;
	}
}
.footer__container {
    align-self: end;
    padding: 15px 15px 5px 15px;
    color: var(--text-head);
    background: var(--bg-header-footer);
    text-align: center;
}

.footer__heading {
    margin-top: 8px;
    margin-bottom: 8px;
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
}

.footer__social-links {
    display: flex;
    justify-content: center;
    list-style-type: none;
}

.footer__social-item {
    padding: 16px;
    font-size: 50px;
}

.footer__link-item {
    color: var(--text-head);
}

.footer__link-item:hover {
    color: rgba(0, 0, 0, 0.5);
}

.footer__copyright {
    font-size: 12px;
    text-align: center;
}

.footer__copyright a {
    color: var(--test-body);
}

.form {
	display: block;
	margin: 15px 30px;
	color: var(--text-body);
    background: var(--bg-form);
	border-radius: 10px;
	padding: 10px;
}

.form__container {
	padding: 20px 10px;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 1fr;
}

.form__label {
    display: block;
    margin-top: 10px;
    padding: 3px;
    font-weight: 600;
}

.form__input {
    display: block;
    margin: 0;
    width: 100%;
	border: 0;
	border-bottom: 1px solid var(--text-body);
	color: var(--text-body);
	height: 1.1876em;
    padding: 6px 3px 7px;
    min-width: 0;
    background: none;
    box-sizing: content-box;
    -webkit-tap-highlight-color: transparent;
}
.form__input:focus {
	border-bottom: 2px solid var(--text-body);
}

.form__btn {
    display: block;
    margin: 30px auto 0;
    border-radius: 5px;
    border: 0;
    height: 36px;
    padding: 0 30px;
    text-transform: uppercase;
    text-decoration: none;
    color: var(--text-body-invert);
    background: var(--form-btn);
    cursor: pointer;
}

.form__btn:hover {
    color: var(--text-body);
}

.form__error {
	margin: 15px 0 0;
	text-align: center;
    font-weight: 700;
}

.form__content {
	grid-row: 1 / 2;
	grid-column: 1 / 2;
}

.form__content * {
	margin: 0 0 10px 0;
}

@media(max-width: 767px) {
	.form {
		margin: 0 0 15px;
		border-radius: 0;
		padding: 10px;
	}

	.form__background {
		display: none;
	}
}
:root {
	--gray0: #f8f8f8;
	--gray1: #dbe1e8;
	--gray2: #b2becd;
	--gray3: #6c7983;
	--gray4: #454e56;
	--gray5: #2a2e35;
	--gray6: #12181b;
	--bg-header-light: #f0f0f0;
	--text-hover-light: var(--gray6);
	--text-hover-dark: var(--gray6);
	--bg-hover-light: #f0f0f0;
	--bg-hover-dark: var(--gray1);
}

.light {
	--text-date-picker: var(--gray6);
	--bg-date-picker-header: var(--bg-header-light);
	--bg-date-picker-body: var(--gray0);
	--text-hover: var(--text-hover-light);
	--bg-hover: var(--bg-hover-light);
}

.dark {
	--text-date-picker: var(--gray0);
	--bg-date-picker-header: var(--gray6);
	--bg-date-picker-body: var(--gray5);
	--text-hover: var(--text-hover-dark);
	--bg-hover: var(--bg-hover-dark);
}

.react-datepicker, 
.react-datepicker__time-list {
	color: var(--text-date-picker);
	background: var(--bg-date-picker-body);
}

.react-datepicker__input-container input {
	display: block;
    margin: 0;
    width: 100%;
	border: 0;
	border-bottom: 1px solid var(--text-body);
	color: var(--text-body);
	height: 1.1876em;
    padding: 6px 3px 7px;
    min-width: 0;
    background: none;
    box-sizing: content-box;
    -webkit-tap-highlight-color: transparent;
}

.react-datepicker__month, 
.react-datepicker__day, 
.react-datepicker__current-month, 
.react-datepicker__day-name, 
.react-datepicker__day, 
.react-datepicker__time-name, 
.react-datepicker-time__header {
	color: var(--text-date-picker);
}

.react-datepicker__day--disabled, 
.react-datepicker__month-text--disabled, 
.react-datepicker__quarter-text--disabled {
	color: #ccc;
}

.react-datepicker-popper {
	max-width: 350px;
}

.react-datepicker__header {
	background: var(--bg-date-picker-header);
} 

.react-datepicker__day:hover, 
.react-datepicker__day-name:hover, 
.react-datepicker__day:hover, 
.react-datepicker__time-name:hover, 
.react-datepicker__time-list-item:hover {
	color: var(--text-hover);
	background: var(--bg-hover);
}
.card__container {
    align-items: center;
    padding-left: 5px;
    position: relative;
    border-radius: 10px;
    display: grid;
    width: 100%;
    height: 120px;
    color: var(--text-body-invert);
    background: var(--bg-card);
    grid-template-columns: -webkit-max-content 1fr 40px;
    grid-template-columns: max-content 1fr 40px;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    grid-row-gap: 5px;
    row-gap: 5px;
}

.card__delete-btn {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 30px;
	height: 30px;
	overflow: hidden;
    color: var(--del-btn);
    cursor: pointer;
}
.card__delete-btn:hover {
    color: var(--del-btn-hover);
}

.card__complete-btn {
    position: absolute;
    bottom: 5px;
    right: 5px;
    width: 30px;
	height: 30px;
	overflow: hidden;
    color: var(--com-btn);
    cursor: pointer;
}
.card__complete-btn:hover {
    color: var(--com-btn-hover);
}

.card__icon-container {
	width: 30px;
    height: 30px;
    border-radius: 15px;
    background: #fff;
}

.card__icon {
	font-size: 30px;
	line-height: 1;
}

.card__heading {
    font-weight: 600;
}

.card__content {
    width: 100%;
}

.card__name {
    overflow: hidden;
    white-space: nowrap;
    overflow-wrap: break-word;
    word-wrap: break-word;
    text-overflow: ellipsis;
}

.card__full-name {
    position: absolute;
    bottom: 103px;
    left: 5px;
    box-sizing: border-box;
    width: calc(100% - 45px);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 3px 5px;
    word-wrap: break-word;
    color: var(--full-name-body);
    background: var(--bg-full-name);
    border-radius: 5px;
    font-size: 14px;
    z-index: 999;
}

.colon {
    display: inline-block;
    margin: 0 2px;
}

@media(max-width: 767px) {
    .card__name {
        max-width: 180px;
    }
}

.list {
	display: block;
	margin: 15px 30px;
	background: var(--bg-list);
	border-radius: 10px;
	padding: 10px;
}

.list__container {
	padding: 20px 10px;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 1fr;
}

.list__title {
	display: grid;
	grid-template-columns: 120px auto;
	justify-items: left;
	align-items: center;
}

.list__delete {
	width: 30px;
	height: 30px;
	overflow: hidden;
	cursor: pointer;
}

.list__icon { 
	font-size: 30px;
    line-height: 1;
}

.list__cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    grid-row-gap: 10px;
    row-gap: 10px;
}

.list__no-data {
	font-weight: 700;
}

.list__expired .section__icon {
    color: var( --icon-expired);
}

.list__expired .card__container {
    background: var(--bg-expired);
}

.list__done .section__icon {
    color: var( --icon-done);
}

.list__done .card__container {
    background: var(--bg-done);
}

@media(max-width: 767px) {
	.list {
		margin: 0 0 15px;
		border-radius: 0;
		padding: 10px;
	}

	.list__container {
		padding: 20px 5px;
	}
}

.ays {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	background: rgba(51, 51, 51, .3);
	z-index: 100;
}

.ays__container {
	position: absolute;
	top: 50px;
	left: calc((100vw - 300px) / 2);
	box-sizing: border-box;
	width: 300px;
	padding: 15px;
	background: rgba(200, 200, 200, .7);
	border: 1px solid var(--text-body-invert);
	border-radius: 10px;
	color: var(--text-body);
}

.ays__btn-container {
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-items: end;
	margin-top: 10px;
}

.ays__btn {
	display: block;
    border-radius: 5px;
    border: 0;
    height: 36px;
    padding: 0 30px;
    text-transform: uppercase;
    text-decoration: none;
    color: var(--text-body-invert);
    background: var(--bg-expired);
    cursor: pointer;
}

.ays__btn-yes {
	justify-self: start;
	background: var(--bg-done);
}
